<template>
  <div class="conflict-container">
    <h1>üß† –†–∞–∑—Ä–µ—à–µ–Ω–∏–µ –∫–æ–Ω—Ñ–ª–∏–∫—Ç–∞</h1>

    <div v-if="!aiResponse">
      <div class="step">
        <label>1. –û–ø–∏—à–∏ –∫–æ–Ω—Ç–µ–∫—Å—Ç –∫–æ–Ω—Ñ–ª–∏–∫—Ç–∞:</label>
        <textarea v-model="context" rows="3" />
      </div>

      <div class="step">
        <label>2. –û–ø–∏—à–∏ —É—á–∞—Å—Ç–Ω–∏–∫–æ–≤ –∫–æ–Ω—Ñ–ª–∏–∫—Ç–∞ (–∏—Ö –ø–æ–≤–µ–¥–µ–Ω–∏–µ, —ç–º–æ—Ü–∏–∏, —Ä–µ–∞–∫—Ü–∏–∏):</label>
        <textarea v-model="participants" rows="3" />
      </div>

      <div class="step">
        <label>3. –ö–∞–∫–∏–µ –¥–µ–π—Å—Ç–≤–∏—è —É–∂–µ –±—ã–ª–∏ –ø—Ä–µ–¥–ø—Ä–∏–Ω—è—Ç—ã –∏ —Å –∫–∞–∫–∏–º —Ä–µ–∑—É–ª—å—Ç–∞—Ç–æ–º?</label>
        <textarea v-model="attempts" rows="3" />
      </div>

      <div class="step">
        <label>4. –ö–∞–∫–æ–≤–∞ —Ü–µ–ª—å ‚Äî —á–µ–≥–æ —Ç—ã —Ö–æ—á–µ—à—å –¥–æ—Å—Ç–∏—á—å?</label>
        <textarea v-model="goal" rows="2" />
      </div>

      <button @click="submitConflict" :disabled="loading">
        {{ loading ? "–û–±—Ä–∞–±–æ—Ç–∫–∞..." : "–ü–æ–ª—É—á–∏—Ç—å —Ä–µ–∫–æ–º–µ–Ω–¥–∞—Ü–∏–∏" }}
      </button>
    </div>

    <div v-if="aiResponse" class="response-block">
      <h2>üìù –†–µ–∫–æ–º–µ–Ω–¥–∞—Ü–∏–∏</h2>
      <div v-html="aiResponse"></div>
      <button @click="resetForm">–ù–æ–≤—ã–π –∑–∞–ø—Ä–æ—Å</button>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      context: "",
      participants: "",
      attempts: "",
      goal: "",
      aiResponse: "",
      loading: false
    };
  },
  methods: {
    async submitConflict() {
      this.loading = true;
      try {
        const response = await fetch("/api/conflict/resolve", {
          method: "POST",
          headers: { "Content-Type": "application/json" },
          body: JSON.stringify({
            context: this.context,
            participants: this.participants,
            attempts: this.attempts,
            goal: this.goal
          })
        });

        const data = await response.json();
        this.aiResponse = data.response || "–ù–µ —É–¥–∞–ª–æ—Å—å –ø–æ–ª—É—á–∏—Ç—å –æ—Ç–≤–µ—Ç.";
      } catch (error) {
        console.error("‚ùå –û—à–∏–±–∫–∞:", error);
        this.aiResponse = "–ü—Ä–æ–∏–∑–æ—à–ª–∞ –æ—à–∏–±–∫–∞ –ø—Ä–∏ –ø–æ–ª—É—á–µ–Ω–∏–∏ –æ—Ç–≤–µ—Ç–∞ –æ—Ç AI.";
      } finally {
        this.loading = false;
      }
    },
    resetForm() {
      this.context = "";
      this.participants = "";
      this.attempts = "";
      this.goal = "";
      this.aiResponse = "";
    }
  }
};
</script>

<style scoped>
.conflict-container {
  max-width: 800px;
  margin: auto;
  padding: 20px;
  background: white;
  border-radius: 16px;
  box-shadow: 0 0 12px rgba(0,0,0,0.08);
}

.step {
  margin-bottom: 20px;
}

textarea {
  width: 100%;
  padding: 10px;
  border-radius: 8px;
  border: 1px solid #ddd;
  font-size: 14px;
  resize: vertical;
}

button {
  background: #7e57c2;
  color: white;
  padding: 12px 24px;
  border: none;
  font-weight: bold;
  border-radius: 8px;
  cursor: pointer;
}

button:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

.response-block {
  background: #f9f9f9;
  padding: 20px;
  border-radius: 12px;
}
</style>
